@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,900&display=swap);
* {
    margin: 0;
    box-sizing: border-box;
    font-family: Poppins;
}

body {
    /* width: 1440px; */
    font-size: 18px;
    background-color: #ffffff;
  }
  
  /* code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  } */
:root {
  --cyan: hsl(180, 66%, 49%);
  --DarkViolet: hsl(257, 27%, 26%);
  --padding: 21px 120px;
  --tag-color: hsl(253, 25%, 26%)
}

.App {
  text-align: center;
  /* border: 5px solid red; */
  margin: 0px 55px;
}


/* Header Styling  */
.header-container {
  width: 100%;
  display: flex;
  padding: 21px 120px;
  padding: var(--padding);
  justify-content: space-between;
}


.header-container .logo {
  text-decoration: none;
  margin-top: 5px;
}

.header-container nav.nav-content {
  width: 85%;
  display: flex;
}

.header-container ul {
  display: flex;
  /* justify-content: center; */
  align-items: center;
}

.header-container ul.feature {
  flex: 1 1;
}

.header-container ul.login {
  flex: 0 1;
}

.header-container ul li {
  list-style: none;
}

.header-container ul li > a {
  text-decoration: none;
  color: #8f8e96;
  font-weight: 600;
  margin: 0 15px;
  font-size: 14px;
}

.header-container ul li > a.signup{
  display: block;
  width: 83px;
  height: 29px;
  background-color: hsl(180, 66%, 49%);
  background-color: var(--cyan);
  padding: 3px 13px;
  margin:0;
  border-radius: 50px;
}
/* End of header styling  */

/* Main Content Styling */

.main .banner {
  background-image: url(/URL-shortening-API-landing-page/static/media/illustration-working.5029d049.svg);
    background-repeat: no-repeat;
    /* background-attachment: fixed; */
    background-position: 121% 17%;
    background-size: 54% 66%;
    height: 700px;
    padding: 21px 120px;
    padding: var(--padding);
    padding-top: 100px;
    text-align: justify;
}

.main .banner h1 {
  font-size: 70px;
  font-weight: bolder;
  line-height: 80px;
}

.main .banner p {
  color: hsl(257, 7%, 63%);
}

.main .banner button, .stat-footer button {
  color: #ffffff;
  background-color: hsl(180, 66%, 49%);
  background-color: var(--cyan);
  width: 152px;
  height: 46px;
  border-radius: 30px;
  border: 0;
  margin-top: 40px;
  font-size: 18px;
  font-weight: 700;
}

.main .shorten {
  width: 80%;
  top: 725px;
  right: 152px;
  position: absolute;
}

.main .shorten > .shorten-form {
  
  margin: auto auto;
  padding: 40px 20px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(75, 64, 107, 0.8)), to(rgba(75, 64, 107, 0.8))), rgba(75, 64, 107, 0.7) url(/URL-shortening-API-landing-page/static/media/bg-shorten-mobile.4a7a7f61.svg) no-repeat center;
  background: linear-gradient(rgba(75, 64, 107, 0.8), rgba(75, 64, 107, 0.8)), rgba(75, 64, 107, 0.7) url(/URL-shortening-API-landing-page/static/media/bg-shorten-mobile.4a7a7f61.svg) no-repeat center;
  border-radius: 10px;
  
  
}

.main .shorten .shorten-form form > input[type="text"] {
  width: 75%;
  height: 64px;
  border-radius: 10px;
  margin-right: 17px;
  outline: none;
  border: 0;
  font-size: 18px;
}

.main .shorten .shorten-form form > button{
  width: 16%;
  height: 63px;
  color: white;
  background-color: hsl(180, 66%, 49%);
  background-color: var(--cyan);
  font-family: poppins;
  border-radius: 10px;
  border: 0;
  font-weight: bold;
  font-size: 18px;
}

/* End of main board */

.stat {
  background-color: rgb(240, 241, 246);
  padding: 21px 120px;
  padding: var(--padding);
  padding-top: 99px;
  padding-bottom: 159px;

}

.stat ul.list {
  padding: 0;
  list-style: none;
  width: 100%;
}

.stat ul.list li {
  background-color: white;
  margin-bottom: 10px;
  padding: 13px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
}

.stat .stat-content {
  padding-top: 100px;
}

.stat .cards {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
  position: relative;
}

.cards .card {
  background-color: white;
  border-radius: 10px;
  width: 350px;
  padding: 84px 20px 37px 20px;
  overflow-wrap: break-word;
  text-align: justify;
  
}

.cards .card.second {
  position: absolute;
  left: 430px;
  top: 30px;
}

.cards .card.third {
  position: absolute;
  right: 0;
  top: 65px;
}

.cards .card > .tag {
  width: 80px;
  height: 80px;
  background: hsl(253, 25%, 26%);
  background: var(--tag-color);
  border-radius: 50%;
  position: absolute;
  top: -36px;
}

.line {
  width: 44%;
    height: 10px;
    background-color: hsl(180, 66%, 49%);
    background-color: var(--cyan);
    position: absolute;
    top: 125px;
    left: 350px;
}

.stat ul.list li > button {
  width: 8%;
  padding: 7px;
  background-color: hsl(180, 66%, 49%);
  background-color: var(--cyan);
  border: 0;
  color: white;
  font-size: 15px;
  font-weight: 500;
  border-radius: 5px;

}

.tag img {
  padding: 17px;
}

.stat ul.list li > button.dark {
  background-color: rgb(75, 63, 107);
}

.stat-footer {
  padding: 65px;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(75, 64, 107, 0.8)), to(rgba(75, 64, 107, 0.8))), rgba(75, 64, 107, 0.7) url(/URL-shortening-API-landing-page/static/media/bg-shorten-mobile.4a7a7f61.svg) no-repeat center;
  background: linear-gradient(rgba(75, 64, 107, 0.8), rgba(75, 64, 107, 0.8)), rgba(75, 64, 107, 0.7) url(/URL-shortening-API-landing-page/static/media/bg-shorten-mobile.4a7a7f61.svg) no-repeat center;

}

.stat-footer > p {
  font-weight: bolder;
  font-size: 35px;
  line-height: 0;
}

.footer {
  background-color: hsl(266, 10%, 14%);
  padding: 21px 120px;
  padding: var(--padding);
  display: flex;
  justify-content: space-between;
  padding-top: 76px;
  padding-bottom: 50px;
}

.footer .footer-content {
  display: inline-flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: justify;
}

.footer .footer-content > p {
  color: white;
  font-weight: bold;
  margin-bottom: 5px;
}

.footer .footer-content > a {
  color: hsl(266, 5%, 74%);
  font-size: 15px;
  margin: 4px;
  text-decoration: none;
}

.footer-content.social {
  flex-direction: row;
}

.footer-content.social img {
  margin: 8px;
  height: 25px;
}

































